@media only screen and (max-width: 1199px) {
  .titleScroll {

    .container {
      margin-left: 100px;
      margin-right: 100px; } } }

@media only screen and (max-width: 1099px) {
  .titleScroll {

    .container {
      margin-left: 50px;
      margin-right: 50px; } } }

@media only screen and (max-width: 991px) {
  .titleScroll {

    .title {

      .subTitle {

        h1 {}

        .bord {} } }

    .content {
      flex-direction: column; } } }

@media only screen and (max-width: 575px) {
  .titleScroll {
    background-image: url('../../public/images/flower2.jpg');
    background-size: cover;
    background-position: center;

    .title {

      .subTitle {

        h1 {
          font-size: 36px;
          font-weight: 700;
          letter-spacing: -0.04em; }

        p {
          font-size: 26px; }

        .bord {
          display: none; } } } } }
