@import "../../utils/dataStyle.module";

.stock {
  padding-top: 15px;

  .container {
    margin-top: 40px;
    position: relative;

    .oldPrice {
      cursor: pointer;
      position: absolute;
      top: 6px;
      left: 40px;
      background: linear-gradient(to top, #A8E1F8, #B4F0FF);
      padding: 5px 10px;
      padding-bottom: 1px;
      border-radius: 4px;
      z-index: 5;
      font-weight: 700;
      box-shadow: $boxShadowMiddle;
      font-size: 18px;
      transform: rotateZ(-10deg);
      color: black; }

    .price {
      cursor: pointer;
      position: absolute;
      top: -20px;
      left: 20px;
      background: linear-gradient(to top, #A8E1F8, #B4F0FF);
      padding: 5px 10px;
      border-radius: 4px;
      color: red;
      font-weight: 700;
      font-size: 18px;
      box-shadow: $boxShadowMiddle;
      transform: rotateZ(-10deg);
      z-index: 20; }

    .sale {
      cursor: pointer;
      position: absolute;
      z-index: 20;
      top: -20px;
      right: 20px;

      img {
        position: relative;
        height: auto;
        width: 50px;
        margin-bottom: -22px;
        z-index: 2;
        margin-right: -4px; }

      span {
        position: relative;
        z-index: 1;
        background: $backHard;
        color: white;
        font-weight: 700;
        font-size: 18px;
        box-shadow: $boxShadowMiddle;
        padding: 5px;
        border-radius: 4px; } }

    .choiceSize {
      position: absolute;
      border-radius: 4px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      bottom: 100px;
      padding: 10px 20px;
      background-color: white;
      box-shadow: $boxShadowMiddle;
      z-index: -2;
      transition: .1s;
      opacity: 0;

      p {
        margin: 0; }

      .submit {
        margin-top: 20px;
        padding: 7px 12px;
        border-radius: 4px;
        border: none;
        color: white;
        font-weight: 700;
        background: $hard;
        transition: .1s;
        box-shadow: 0 0 3px 0 $hard;

        &:hover {
          transition: .2s;
          box-shadow: 0 0 1px 0 $hard; } }

      .size {
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        padding-top: 3px;
        border-radius: 4px;
        height: 35px;
        width: 40px;
        margin-right: 3px;
        transition: .2s;
        border: 1px solid #e2e3e5; }

      .activeSize {
        transition: .2s;
        border-color: $hard;
        box-shadow: $boxShadowLight; } }

    .viewChoiceSize {
      z-index: 25;
      transition: .2s;
      opacity: 1; }

    .pushBasket {
      cursor: pointer;
      position: absolute;
      z-index: 21;
      bottom: 0;

      &:hover {
        border: none;

        img {
          transition: .2s;
          box-shadow: $boxShadowLight; } }

      .basketIcon {
        width: 100%;
        text-align: center;
        color: white;
        text-shadow: $textShadow;
        font-size: 22px;
        position: relative;
        padding-top: 10px;

        i {
          margin-right: 3px; }

        &:before {
          position: absolute;
          content: '';
          top: -2px;
          left: 50%;
          height: 10px;
          width: 10px;
          border-left: 2px solid white;
          border-bottom: 2px solid white;
          transform: translateX(-50%) rotateZ(-45deg); } }

      img {
        margin-top: 4px;
        height: auto;
        width: 50px;
        border-radius: 100%;
        transition: .1s;
        box-shadow: $boxShadowMiddle; } }

    .img {
      display: flex;
      justify-content: center;
      align-items: center;

      .imgContainer {
        transform: translateX(5%) rotateZ(-10deg);

        img {
          height: auto;
          max-width: 100%;
          cursor: pointer;
          transition: .2s;
          box-shadow: $boxShadowLight;

          &:hover {
            transition: .2s;
            box-shadow: $boxShadowMiddle; } }

        &:last-child {
          transform: translateX(-5%) rotateZ(-10deg); } } } } }


.dataProdContactForm {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  font-size: 18px;

  span {
    display: block;
    min-width: 200px; } }


@import "./media";

