@import "../../utils/dataStyle.module";

.basket {
  font-family: 'Montserrat', sans-serif!important;
  background-color: white;
  padding: 5px;
  max-width: 1000px;
  margin-top: 5px;
  border-radius: 4px;
  box-shadow: $boxShadowLight;

  .content {
    width: 98%;

    .allPrice {
      font-weight: 700;
      font-size: 22px; }

    .inactive {

      span {
        transition: .2s;
        color: #c8c8c8; } }

    .submit {
      padding-top: 15px;
      padding-bottom: 15px;

      button {
        padding: 10px 25px;
        border-radius: 4px;
        border: none;
        color: white;
        font-weight: 700;
        background: $hard;
        transition: .1s;
        box-shadow: 0 0 10px 0 $hard;

        &:hover {
          transition: .2s;
          box-shadow: 0 0 5px 0 $hard; } } }

    .disabled {

      button {
        transition: .2s;
        border-color: #c8c8c8;
        color: #c8c8c8; } }

    .product {
      display: flex;
      align-items: center;
      justify-content: space-between;
      box-shadow: $boxShadowLight;
      border-radius: 4px;
      padding: 3px 0;
      margin: 5px 0;

      .price {
        display: flex;
        flex-direction: column;

        span {
          font-size: 16px;
          font-weight: 500;
          transition: .2s;
          color: $txt; }

        .priceSpan {
          margin-left: 3px; } }

      .index {
        padding: 10px;

        span {
          color: $txt;

          &:first-child {
            align-self: flex-start; } } }

      .imgBox {
        max-width: 110px;

        img {
          height: auto;
          max-width: 100%; } }

      .name {
        width: 100%;
        padding: 3px 0;
        text-align: center;
        position: relative;
        border: 1px solid rgba(0,0,0,.1);
        border-radius: 4px;
        height: 100%;
        font-size: 14px;

        span {
          display: block;
          color: $txt; }

        input {
          width: 80px;
          border-radius: 4px;
          padding: 5px 3px;
          color: $txt;
          transition: .2s;
          border: 1px solid #eee;

          &::-webkit-inner-spin-button {
            display: none; }

          &:focus {
            transition: .2s;
            border-color: $hard; } } }

      .delete {
        margin-top: 5px;
        padding: 5px;
        margin-right: 10px;

        button {
          padding: 5px 10px;
          border: none;
          color: $txt;
          background: none;
          border-radius: 4px;
          transition: .2s;
          box-shadow: $bsBasket;

          &:hover {
            transition: .2s;
            box-shadow: 0 1px 10px 0 rgba(255,0,0,.2); } } } } } }

.bord {
  position: absolute;
  top: -10px;
  bottom: -10px;
  right: -5px;
  width: 1px;
  background-color: rgba(0,0,0,.2); }

.question {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 4px;
  padding-top: 20px;
  padding-left: 20px;
  padding-right: 20px;
  margin-top: 10px;
  background: white;
  box-shadow: $boxShadowLight;

  span {
    color: black;
    font-size: 24px; } }

.questionBtns {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  button {
    margin: 15px 10px;
    padding: 10px 30px;
    background: rgba(255,255,255,.8);
    border: 1px solid $hard;
    border-radius: 4px;
    transition: .2s;
    box-shadow: $boxShadowLight;

    &:hover {
      transition: .2s;
      box-shadow: $bsInputFocus; } } }

.basketLink {
  font-family: 'El Messiri', sans-serif;
  cursor: pointer;
  height: 50px;
  width: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $hardRgba;
  box-shadow: $bsBasket;
  top: 100px;
  border-radius: 100%;
  right: 10px;
  z-index: 1000;
  position: fixed;

  i {
    font-size: 24px;
    color: white;
    text-shadow: $textShadow; }

  &:hover {

    i {
      transition: .2s; } }

  .counter {
    position: relative;
    top: -25px;
    left: 10px;
    z-index: 1001;

    p {
      position: absolute;
      margin: 0;
      text-align: center;
      color: white;
      text-shadow: $textShadow; } } }

@import "./media";
