@import url('https://fonts.googleapis.com/css?family=Amatic+SC:400,700|Playfair+Display:400,400i,700,700i,900,900i&subset=cyrillic');
@import "../../utils/dataStyle.module";

.contactForm {
  width: 100%;
  padding-top: 15px;

  .container {
    padding: 20px;
    background-color: white;
    position: relative;
    box-shadow: $boxShadowLight;
    border-radius: 4px;

    .bord {
      position: absolute;
      height: 8px;
      background-color: black;
      top: auto;
      right: -20px;
      left: 0;
      bottom: 2px;
      transform: skewY(-1deg);
      box-shadow: $boxShadowLight;

      &:last-child {
        top: 2px;
        bottom: auto;
        left: -20px;
        right: 0;
        margin-bottom: 50px;
        transform: skewY(1deg); } }

    .title {
      margin-left: auto;
      margin-right: auto;
      max-width: 200px;
      font-weight: 600;
      display: flex;
      flex-direction: column;
      color: $txt;
      justify-content: center;
      font-size: 22px;
      text-align: center;

      img {
        height: auto;
        max-width: 50px;
        margin: 0 auto; } }

    .inputs {
      position: relative;
      width: 100%;
      padding: 20px 0;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      .inputContainer {
        margin: 5px 0;
        width: 100%;
        display: flex;
        justify-content: center;

        .submitBtn {
          border-radius: 4px;
          background: white;
          color: #858585;
          font-size: 18px;
          min-width: 200px;
          padding: 10px 0;
          transition: .1s;
          border: 1px solid transparent; }

        .activeSubmit {
          transition: .2s;
          color: $txt;
          border-color: $txt;
          box-shadow: $boxShadowLight; }

        input {
          border-radius: 4px;
          font-size: 18px;
          width: 100%;
          letter-spacing: -1px;
          padding: 10px;
          color: $txt;
          transition: .2s;
          border: 1px solid $txt;

          &:focus {
            transition: .2s;
            box-shadow: $bsInputFocus; }

          &::placeholder {
            letter-spacing: -1px;
            font-size: 18px; } } } } } }



@import "./media";
