@import "../../utils/dataStyle.module";

.aboutUs {
  padding-top: 15px;
  padding-bottom: 30px;
  text-align: center;

  p {
    margin: 10px 0;
    box-shadow: $boxShadowLight;
    padding: 10px;
    border-radius: 4px;
    color: $txt; }

  .first {
    margin-top: 15px; }

  .second {
    margin-top: 5px; }

  hr {
    border: 0;
    height: 1px;
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0), $hardRgba, rgba(0, 0, 0, 0)); } }
